<template>
    <main class="content container">
        <div class="content__top content__top--catalog">
            <h1 class="content__title">Каталог</h1>
            <span class="content__info">{{ countProducts }} товара</span>
        </div>

        <div class="content__catalog">
            <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :filter-color-id.sync="filterColorId" :items="products" />


            <section class="catalog">
                <div v-if="productLoading">Загрузка товаров...</div>
                <div v-if="productsLoadingFailed">
                    Произошла ошибка при загрузке товаров
                    <button @click.prevent="loadProducts">Попробовать еще раз</button>
                </div>
                <ProductList :products="products" />
                <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" :content="productsData" />
            </section>
        </div>
    </main>
</template>

<script>
import ProductList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue";
import axios from "axios";
import { API_BASE_URL } from "@/config";

export default {
    components: { ProductList, BasePagination, ProductFilter },
    data() {
        return {
            filterPriceFrom: 0,
            filterPriceTo: 0,
            filterCategoryId: 0,
            page: 1,
            productsPerPage: 3,
            filterColorId: 0,

            productsData: null,
            productLoading: false,
            productsLoadingFailed: false,
        };
    },
    computed: {
        products() {
            return this.productsData
                ? this.productsData.items.map((product) => {
                    return {
                        ...product,
                        image: product.image.file.url,
                    };
                })
                : [];
        },
        countProducts() {
            return this.productsData ? this.productsData.pagination.total : 0;
        },
    },
    methods: {
        loadProducts() {
            this.productLoading = true;
            this.productsLoadingFailed = false;
            clearTimeout(this.loadProductsTimer);
            this.loadProductsTimer = setTimeout(() => {
                axios
                    .get(API_BASE_URL + `/api/products?`, {
                        params: {
                            page: this.page,
                            limit: this.productsPerPage,
                            categoryId: this.filterCategoryId,
                            minPrice: this.filterPriceFrom,
                            maxPrice: this.filterPriceTo,
                            colorId: this.filterColorId,
                        },
                    })
                    .then((response) => (this.productsData = response.data))
                    .catch(() => (this.productsLoadingFailed = true))
                    .then(() => (this.productLoading = false));
            }, 0);
        },
    },
    watch: {
        page() {
            this.loadProducts();
        },
        filterPriceFrom() {
            this.loadProducts();
        },
        filterPriceTo() {
            this.loadProducts();
        },
        filterCategoryId() {
            this.loadProducts();
        },
         filterColorId() {
            this.loadProducts();
        },
    },
    created() {
        this.loadProducts();
    },
};
</script>
