<template>
  <li class="catalog__item">
    <router-link class="catalog__pic" :to="{name:'product', params:{id:product.id}}" >
      <img :src="product.image" :alt="product.title">
    </router-link>
    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>
    <span class="catalog__price">
      {{ product.price | numberFormat }} ₽
    </span>
    <ul class="colors colors--black">
      <li class="colors__item" v-for="(color, index) in product.colors" :key="index">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="color" v-model="selectedColor" @change="saveSelectedColor">
          <span class="colors__value" :style="{ backgroundColor: color.code }"></span>
        </label>
      </li>
    </ul>
  </li>
</template>

<script>
import gotoPage from '@/helpers/gotoPage';
import numberFormat from '@/helpers/numberFormat'
export default {
  props: ['product'],
  data() {
    return {
      selectedColor: null
    };
  },
  filters: {
    numberFormat
  },
  created() {
   
    const savedColor = localStorage.getItem(`selectedColor_${this.product.id}`);
    if (savedColor) {
      this.selectedColor = JSON.parse(savedColor);
    } else {
      
      this.selectedColor = this.product.colors[0];
    }
  },
  methods: {
    saveSelectedColor() {
      
      localStorage.setItem(`selectedColor_${this.product.id}`, JSON.stringify(this.selectedColor));
    },
    gotoPage
   
  }
};
</script>
