<template>
  <div>
    <ul class="catalog__pagination pagination">
      <li class="pagination__item">
        <a class="pagination__link pagination__link--arrow"
          :class="{ 'pagination__link--disabled': page === 1, 'pointer': page >= 1 }" aria-label="Предыдущая страница"
          @click.prevent="page !== 1 && paginate(page - 1)">
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-left"></use>
          </svg>
        </a>
      </li>
      <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
        <a href="#" class="pagination__link" :class="{ 'pagination__link--current': pageNumber === page }"
          @click.prevent="page !== pageNumber && paginate(pageNumber)"> <!-- Используйте имя события paginate -->
          {{ pageNumber }}
        </a>
      </li>
      <li class="pagination__item">
        <a class="pagination__link pagination__link--arrow"
          :class="{ 'pagination__link--disabled': page === pages, 'pointer': page < pages }" href="#"
          aria-label="Следующая страница" @click.prevent="page !== pages && paginate(page + 1)">
          <svg width="8" height="14" fill="currentColor">
            <use xlink:href="#icon-arrow-right"></use>
          </svg>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage', 'content'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      if (page >= 1 && page <= this.pages) { 
        this.$emit('paginate', page);
      }
    },
  },
};
</script>
