<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ 23621</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа.
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
                {{ FormData.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{ FormData.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{ FormData.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{ FormData.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                {{ orderPaymentMethod }}
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
    <ul class="cart__orders">
      <li class="cart__order" v-for="item in orderInfo.basket.items">
        <h3>{{ item.product.title }}</h3>
        <b>{{ (item.quantity * item.product.price) | numberFormat }} ₽</b>
        <b>Кол-во - {{ item.quantity }}</b>
        <span>Артикул: {{ item.product.id }}</span>
      </li>
    </ul>
    <div class="cart__total" v-for="item in orderInfo.basket.items">
      <p>Доставка: <b>500 ₽</b></p>
      <p>Итого: <b>{{ item.quantity | numberFormat }}</b> товара на сумму <b>{{ orderInfo.totalPrice + 500 | numberFormat }} ₽</b></p>
    </div>
  </div>
          
      </form>
    </section>
  </main>
</template>

<script>

import OrderPage from '@/pages/OrderPage.vue';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import { API_BASE_URL } from '@/config';
import axios from 'axios';

export default {
  filters: {
    numberFormat
  },
  components: { OrderPage },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', countProducts: 'cartItemCount' }),

    orderPaymentMethod() {
      return this.$store.state.orderPaymentMethod;
    },
    orderInfo() {
      return this.$store.state.orderInfo;
    },

    FormData() {
      return this.$store.state.orderInfo;
    },
    orderItems() {
      return this.$store.state.orderInfo.items;
    }

  },
  created() {
    if (this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
      return;
    }
    this.$store.dispatch('loadOrderInfo', this.$route.params.id)
  },
 methods: {
   orderIn() {
      axios
        .get(API_BASE_URL + '/api/orders/' + this.$route.params.id, {
          params: {
            userAccessKey: this.$store.state.userAccessKey
          }
        })
        .then(response => {
          this.orderInfo = response.data; 
        })
       
    }
  }
}

</script>